// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import FastClick from 'fastclick'
import VueRouter from 'vue-router'
import App from './App'
window.$ = require('jquery')

require('font-awesome-webpack');

// import Home from './components/HelloFromVux'
const HelloFromVux = resolve => require(['./components/HelloFromVux'], resolve);
const surprise = resolve => require(['./components/surprise'], resolve);
const word = resolve => require(['./components/word'], resolve);
const heart = resolve => require(['./components/heart'], resolve);

// import Hello from './components/Hello'
const Hello = resolve => require(['./components/Hello'], resolve);

// import home from './components/home'
const home = resolve => require(['./components/home'], resolve);



Vue.use(VueRouter)

const routes = [
	{
	  path: '/',
	  component: home
	},
	{
		path: '/home/',
		component: home,
		children: [
			{
				path: 'HelloFromVux',
				component: HelloFromVux
			},
			{
				path: 'surprise',
				component: surprise
			},
			{
				path: 'word',
				component: word
			},
			{
				path: 'heart',
				component: heart
			}
		]
	},
	{
		path: '/hello',
		component: Hello
	}
]

const router = new VueRouter({
  routes
})

FastClick.attach(document.body)

Vue.config.productionTip = false

/* eslint-disable no-new */
new Vue({
  router,
  render: h => h(App)
}).$mount('#app-box')



